<template lang="html">
  <div class="">


  <h2>Login</h2>

  <div class="row">
    <div class="card mx-auto">
      <div class="card-header test-white bg-primary">
        <h4>Login</h4>
      </div>
      <div class="card-body">
        <form @submit.prevent="loginUser">
          <div class="form-group">
            <label for="username">Username</label>
            <input
              class="form-control"
              type="text"
              placeholder="Username"
              name="username"
              v-model="username"
              id="username"
              >
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input
              class="form-control"
              type="password"
              placeholder="Password"
              name="password"
              v-model="password"
              id="password"
              >
          </div>
          <input type="submit" class="btn btn-primary" value="Login"/>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <router-link to="/register" class="card-link">Need an account?</router-link>

        </form>

      </div>
    </div>
  </div>
    </div>
</template>

<script>
import {mapActions} from 'vuex';

export default {
  data(){
    return{
      username:"",
      password:""
    };
  },
  methods:{
    ...mapActions(['login']),
    loginUser(){
    //  console.log(this.username,this.password)
      // eslint-disable-next-line no-unused-vars
    let user = {
      username: this.username,
      password: this.password
    };
    this.login(user)
    .then(res=>{
      if(res.data.success){
        this.$router.push('/profile');
      }
    }).catch(err =>{
      console.log(err);
    });
    }
  }
};
</script>

<style lang="css" scoped>

.card{
  width: 60%;
  border-radius: 0;
}
.btn{
  width: 60%;
  border-radius: 0;
}
.form-control{
  border-radius: 0;
}
</style>
